.footer .menu-catalog {
				margin: 0;
}

.footer .menu-catalog__padding {
				margin: 0;
}

.footer .menu-catalog__row {
				margin: 0;
}

.footer .menu-catalog__align {
				max-height: 373px;
				display: flex;
				flex-direction: column;
				flex-wrap: wrap;
				overflow: hidden;
}

@media (max-width: 600px) {
				.footer .menu-catalog__align {
								column-count: 3;
								text-align: center;
								display: block;
				}
}

@media (max-width: 480px) {
				.footer .menu-catalog__align {
								column-count: 2;
								text-align: left;
				}
}

.footer .menu-catalog__title {
				color: #2f4b55;
				font-family: "Lato-Bold", -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
				font-size: 15px;
				font-weight: 400;
				line-height: 15px;
				color: #2f4b55;
				letter-spacing: 0.6px;
				margin: 0 0 16px;
}

@media (max-width: 600px) {
				.footer .menu-catalog__title {
								text-align: center;
				}
}

.footer .menu-catalog__item {
				color: #afbac3;
				margin: 0 0 8px;
}

.footer .menu-catalog__link {
				color: #718391;
				font-family: "Lato", -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
				font-size: 13px;
				font-weight: 300;
				line-height: 24px;
				letter-spacing: 0.52px;
}

